.container{
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 11px;
    margin: 13px 1%;
    margin-bottom: 53px;    
    &>h1{
        align-self: center;
    }
    &>ol{
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        &>li{
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            padding: 8px 11px;      
            margin: 14px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            border-radius: 6px;
            cursor: pointer;
            background-color: dodgerblue;;
            &>img:hover{
                @media (hover: hover){
                    transform: scale3d(3,3,1);
                }
            }
            &>img:active{
                transition: transform .3s;
                transform: scale3d(1.3,1.3,1);
            }
        }
    }
}
.forDialog{
    &>img{
        max-width: 80%;
        max-height: 80%;
    }
}
.forCloseButton{
    position: absolute;
    right: 19px;
    top: 14px;
    border: none;
    background-color: transparent;
    font-size: 33px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    &:hover{
        transform: scale3d(1.6,1.6,1);
    
    &:active{
        transition: transform .1s;
            }transform: scale3d(1.3,1.3,1);
    }
}

@mixin buttonprops{
    width: 40px;
    height: 40px;
    padding: 10px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    background-color: #fff;
    font-size: 18px;
    margin: 11px;
    &:active,&:hover{
        transform: scale(1.1)
    }
}
.forPrevButton{
    @include buttonprops;
}
.forNextButton{
    @include buttonprops;
}