.container{
    display: flex;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin:0 1%;
    position: sticky;
    top: 0;
    background-color: aquamarine;
    overflow-x: auto;
    &>a{
        padding: 11px;
        text-decoration: none;
        color: navy;
        white-space: pre;
        &:hover,&:active{
            color: aquamarine;
            background-color: navy;
        }
    }
}