.container{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding:1%;
    margin:13px 1%;
    display: flex;
    flex-direction: column;
    &>h1{
        align-self: center;
    }
    &>div{
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        padding:11px;
        margin:1%;
        border-radius: 9px;
    }
}
.forLinkComponent{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &>p{
        font-weight: bold;
    }
    &>button{
        padding:6px 11px;
        margin: 4px;
        transition-duration: 0.4s;
        border: 3px solid #008CBA;
        cursor: pointer;
        border-radius: 6px;
    }
}
.forTabButton{
    border: 3px solid #008CBA;  
}
.forCopyButton{
    border: 3px solid #008CBA;
}