.container{
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 11px;
    margin: 13px 1%;
    overflow-x: scroll;
    &>h1{
        align-self: center;
    }
    &>table{
        &>tr{
            &>td,>th{
                padding: 16px;
                min-width: fit-content;
                text-align: left;
            }
        }
    }
}
table,th,td{
    border:1px solid;
    border-collapse: collapse;
    overflow: auto;
}